<template>
    <v-app>
        <v-content>
            <router-view></router-view>
        </v-content>
        <template v-if="error">
            <v-snackbar
                    class="normalCd"
                    :timeout="5000"
                    :multi-line="true"
                    color="error"
                    @input="closeError"
                    :value="true"
            >
                {{error}}
                <v-btn dark @click.native="closeError">Закрыть</v-btn>
            </v-snackbar>
        </template>
    </v-app>
</template>

<script>
    export default {
        computed: {
            error() {
                return this.$store.getters.error
            }
        },
        methods: {
            closeError() {
                this.$store.dispatch('clearError')
            }
        }
    }
</script>

<style scoped>
    .normalCd {
        border-radius: 60px;
    }
</style>
