<script>
    setTimeout(() => {
        document.location.reload();
    }, 600000);
    export default {

        data() {
            return {
                btn1: false, btn2: false, btn3: false,
                btn4: false, btn5: false, btn6: false,
                btn7: false, btn8: false, btn9: false,
                btn10: false, btn11: false, btn12: false,
                cl: false,
                val1_1: 0, val1_2: 0, val1_3: 0,
                val2_1: 0, val2_2: 0, val2_3: 0,
                val3_1: 0, val3_2: 0, val3_3: 0,
                val4_1: 0, val4_2: 0, val4_3: 0,
                img1: '',
                img2: '',
                img3: '',
                img4: '',
                src: 'http://e.citynet.uz',
                data1: '',
                data2: '',
                data3: '',
                data4: '',
                fdId1: '',
                fdId2: '',
                fdId3: '',
                fdId4: '',
                snackbar: false,
                ran: 'Рановато!',
                poz: 'Поздновато!',
                txt: '',
                uv1: true, uv2: true, uv3: true,
                uv4: true, uv5: true, uv6: true,
                uv7: true, uv8: true, uv9: true,
                uv10: true, uv11: true, uv12: true
            }
        },
        computed: {
            // foods() {
            //     return this.$store.getters.foods
            // },
            loading() {
                return this.$store.getters.loading
            }
        },

        beforeCreate() {
            let path = '';
            path = this.$route.path;

            this.$http.get(`http://e.citynet.uz/api/foods/${path.replace('/', '')}`).then(req => {
                this.data1 = req.data.data[0];
                this.fdId1 = req.data.data[0].id;
                this.img1 = req.data.data[0].img;
            });
            this.$http.get(`http://e.citynet.uz/api/foods/${path.replace('/', '')}`).then(req => {
                this.data2 = req.data.data[1];
                this.fdId2 = req.data.data[1].id;
                this.img2 = req.data.data[1].img;
            });
            this.$http.get(`http://e.citynet.uz/api/foods/${path.replace('/', '')}`).then(req => {
                this.data3 = req.data.data[2];
                this.fdId3 = req.data.data[2].id;
                this.img3 = req.data.data[2].img;
            });
            this.$http.get(`http://e.citynet.uz/api/foods/${path.replace('/', '')}`).then(req => {
                this.data4 = req.data.data[3];
                this.fdId4 = req.data.data[3].id;
                this.img4 = req.data.data[3].img;
            });
        },

        mounted() {
            this.$http.get('http://e.citynet.uz/api/mark').then(req => {
                let Good1 = 0;
                let Norm1 = 0;
                let Bad1 = 0;
                let Good2 = 0;
                let Norm2 = 0;
                let Bad2 = 0;
                let Good3 = 0;
                let Norm3 = 0;
                let Bad3 = 0;
                let Good4 = 0;
                let Norm4 = 0;
                let Bad4 = 0;
                req.data.data.map(res => {
                    if (res.mark === 3 && res.food_id === this.fdId1) {
                        Good1 = Good1 + 1;
                    }
                    if (res.mark === 2 && res.food_id === this.fdId1) {
                        Norm1 = Norm1 + 1;
                    }
                    if (res.mark === 1 && res.food_id === this.fdId1) {
                        Bad1 = Bad1 + 1;
                    }

                    //////////////////

                    if (res.mark === 3 && res.food_id === this.fdId2) {
                        Good2 = Good2 + 1;
                    }
                    if (res.mark === 2 && res.food_id === this.fdId2) {
                        Norm2 = Norm2 + 1;
                    }
                    if (res.mark === 1 && res.food_id === this.fdId2) {
                        Bad2 = Bad2 + 1;
                    }

                    //////////////////

                    if (res.mark === 3 && res.food_id === this.fdId3) {
                        Good3 = Good3 + 1;
                    }
                    if (res.mark === 2 && res.food_id === this.fdId3) {
                        Norm3 = Norm3 + 1;
                    }
                    if (res.mark === 1 && res.food_id === this.fdId3) {
                        Bad3 = Bad3 + 1;
                    }

                    //////////////////

                    if (res.mark === 3 && res.food_id === this.fdId4) {
                        Good4 = Good4 + 1;
                    }
                    if (res.mark === 2 && res.food_id === this.fdId4) {
                        Norm4 = Norm4 + 1;
                    }
                    if (res.mark === 1 && res.food_id === this.fdId4) {
                        Bad4 = Bad4 + 1;
                    }
                });
                this.val1_1 = 100 * Good1 / 100;
                this.val1_2 = 100 * Norm1 / 100;
                this.val1_3 = 100 * Bad1 / 100;

                //////////////////

                this.val2_1 = 100 * Good2 / 100;
                this.val2_2 = 100 * Norm2 / 100;
                this.val2_3 = 100 * Bad2 / 100;

                //////////////////

                this.val3_1 = 100 * Good3 / 100;
                this.val3_2 = 100 * Norm3 / 100;
                this.val3_3 = 100 * Bad3 / 100;

                //////////////////

                this.val4_1 = 100 * Good4 / 100;
                this.val4_2 = 100 * Norm4 / 100;
                this.val4_3 = 100 * Bad4 / 100;
            });
        },

        // created() {
        //     let path = '';
        //     path = this.$route.path;
        //
        //     let time = '';
