        // },

        methods: {
            good1() {
                this.uv1 = false;
                this.uv2 = false;
                this.uv3 = false;
                this.btn1 = true;
                this.btn2 = true;
                this.btn3 = true;
                setTimeout(() => {
                    let path;
                    path = this.$route.path;
                    this.cl = !this.cl;
                    let data = new FormData();
                    data.append('mark', '3');
                    data.append('organization_id', path.replace('/', ''));
                    data.append('food_id', this.fdId1);
                    this.$http.post('http://e.citynet.uz/api/mark', data).then(() => {
                        this.$http.get('http://e.citynet.uz/api/mark').then(req => {
                            let Good1 = 0;
                            req.data.data.map(res => {
                                if (res.mark === 3 && res.food_id === this.fdId1) {
                                    Good1 = Good1 + 1;
                                }
                            });
                            this.val1_1 = 100 * Good1 / 100;
                        });
                    });
                    this.uv1 = true;
                    this.uv2 = true;
                    this.uv3 = true;
                    this.btn1 = false;
                    this.btn2 = false;
                    this.btn3 = false;
                }, 3000) //5 second request on base
            },
            norm1() {
                this.uv1 = false;
                this.uv2 = false;
                this.uv3 = false;
                this.btn2 = true;
                this.btn1 = true;
                this.btn3 = true;
                setTimeout(() => {
                    let path;
                    path = this.$route.path;
                    this.cl = !this.cl;
                    let data = new FormData();
                    data.append('mark', '2');
                    data.append('organization_id', path.replace('/', ''));
                    data.append('food_id', this.fdId1);
                    this.$http.post('http://e.citynet.uz/api/mark', data).then(() => {
                        this.$http.get('http://e.citynet.uz/api/mark').then(req => {
                            let Norm1 = 0;
                            req.data.data.map(res => {
                                if (res.mark === 2 && res.food_id === this.fdId1) {
                                    Norm1 = Norm1 + 1;
                                }
                            });
                            this.val1_2 = 100 * Norm1 / 100;
                        });
                    });
                    this.uv1 = true;
                    this.uv2 = true;
                    this.uv3 = true;
                    this.btn2 = false;
                    this.btn1 = false;
                    this.btn3 = false;
                }, 3000)
            },
            bad1() {
                this.uv1 = false;
                this.uv2 = false;
                this.uv3 = false;
                this.btn3 = true;
                this.btn2 = true;
                this.btn1 = true;
                setTimeout(() => {
                    let path;
                    path = this.$route.path;
                    this.cl = !this.cl;
                    let data = new FormData();
                    data.append('mark', '1');
                    data.append('organization_id', path.replace('/', ''));
                    data.append('food_id', this.fdId1);
                    this.$http.post('http://e.citynet.uz/api/mark', data).then(() => {
                        this.$http.get('http://e.citynet.uz/api/mark').then(req => {
                            let Bad1 = 0;
                            req.data.data.map(res => {
                                if (res.mark === 1 && res.food_id === this.fdId1) {
                                    Bad1 = Bad1 + 1;
                                }
                            });
                            this.val1_3 = 100 * Bad1 / 100;
                        });
                    });
